/* ------------------------------------------<1: SETUP>------------------------------------------ */
html {
  font-family: -apple-system,".SFNSText-Regular","San Francisco","Roboto","Segoe UI","Helvetica Neue","Lucida Grande",Arial,sans-serif;
  overflow-y: scroll;
  overflow-x: hidden;
}

body {
  padding: 0; 
  margin: 0;
  background: linear-gradient(
      180deg,
      var(--bg1),    /* transition from orange(#ff4400) to blue(#1666c1), at an angle of 132deg */
      var(--bg2)
  );
  background-size: 1000% 1000%;
  animation: animation_name 25s ease infinite;    /* lasts 30s, eases in, and is infinite */
}

@keyframes animation_name {
  /*                          (x,  y) */
  0% { background-position:   0%   50%; }     /* x=top left, y=centre */
  50% { background-position:  100% 50%; }     /* x=bottom left, y=centre */
  100% { background-position: 0%   50%; }     /* x=top left, y=centre */
}

#particles-js canvas{
  position: fixed;
  width: 100%;
  height: 100vh;
}
/* only put the core elements on top of #particles-js, not the entire page because it removes the interactivity */
#about p, #about a, #about h2, #projects .project {
  z-index: 1;
  position: relative;
}

/* ------------------------------------------<2: NAVBAR (includes top and side navigation)>------------------------------------------ */
header {
  height: var(--header-height);
  width: 100vw;
  position: fixed;
  z-index: 10;
  opacity: 0.8;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header ul {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.left-navbar {
  position: absolute;
  margin-top: 40px;
  margin-left: 20px;
  width: 70px;
  display: flex;
  flex-direction: column; 
  justify-content: space-between;
}

header .sidebar {
  background-color: var(--sidebar-bg);
  box-shadow: 0 1px 4px 0 rgba(68, 68, 150, 0.178);
  display: none;
  width: 150px;
  position: fixed;
  transition: opacity 200ms ease-out;
  opacity: 0;
  top: 60px;
  margin-left: 70px;
  margin-top: -45px;
}
header .sidebar.active {
  display: block;
  opacity: 1;
}

header .sidebar ul a {
  color: var(--header-text);
  font-weight: 700;
  display: block;
  padding: 7px 16px;
  margin: 0px 2px;
  list-style-type: none;
  text-decoration: none;
  cursor: pointer;
  transition: all 250ms ease 0s;
  list-style-type: none;
  text-decoration: none;
  border: 1px dotted transparent;
}

header .sidebar ul a:hover {
  color: var(--header-btn-hover);
}
header .sidebar ul a:focus {
  color: var(--header-btn-focus);
  border: 1px dashed black;
  transition: border 300ms ease-in;
}
header .sidebar ul a.active {
  color: var(--header-btn-focus);
  border: 1px dashed black;
}

