#toggle {
  display: none;
}

#toggle:checked ~ .left-navbar {
  left: var(--sidebar-width);
}

#toggle:checked ~ .sidebar {
  left: 0;
}

svg.logo {
  width: 65px;
  height: 65px;
  stroke: rgb(26, 9, 39);
  stroke-width: 0.1px;
  stroke-opacity: 0.11;
  z-index: 10;
}

svg.logo polygon:hover {
  cursor: pointer;
  fill-opacity: 0.50;
}

.sw {fill:none;stroke:black;stroke-width:3px;}
.fo {fill-opacity:0.2;}
.color1 {fill:red;}
.color2 {fill:lime;}
.color3 {fill:blue;}

svg.logo polygon.a { fill: rgb(177, 170, 89); fill-opacity: 1; }
svg.logo polygon.b { fill: rgb(255, 239, 69); fill-opacity: 0.84; }
svg.logo polygon.c { fill: rgb(255, 192, 203); fill-opacity: 0.28;  }
svg.logo polygon.d { fill: rgb(193, 208, 96); fill-opacity: 0.64; }
svg.logo polygon.e { fill: rgb(36, 94, 170); fill-opacity: 0.41; }
svg.logo polygon.f { fill: rgb(63, 67, 31); fill-opacity: 0.29; }

svg.logo polygon:hover { 
  cursor: pointer;
  fill-opacity: 0.49;
  fill: rgb(67, 177, 175);
}

.hamburger-menu-icon {
  position: absolute;
  right: 40px;
  transition: 0.3s all;
  display: flex;
  align-items: center;
}

.hamburger-menu-icon label[for=toggle] {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  height: 25px;
  justify-content: space-between;
}

.hamburger-menu-icon label[for=toggle] span {  
  background-color: var(--header-text);
  display: block;
  height: 5px;
  width: 30px;
  border-radius: 50px;
}


/* ------------------------------------------<3: MAIN CONTAINER>------------------------------------------ */
.page-wrapper {
  font-size: 2.5rem;
  padding: 10px;
  z-index: 999;
  height: 100vh;
}

.page-wrapper.university,
.page-wrapper.universityAssignment,
.page-wrapper.blog,
.page-wrapper.blog-post,
.page-wrapper.notes {
  background-color: rgb(251, 251, 251);
}

.page-title {
  color: var(--headings);
  font-family: 'Oswald', sans-serif;
  text-transform: uppercase;
  text-align: center;
}

#about-desc p {
  color: var(--introtext);
  font-weight: 200;
}

*:focus {
  outline: 0;
  outline: none;
}

/* ------------------------------------------<4: #about SECTION>------------------------------------------ */
#about, #projects, #contact {
  height: 100vh;
  padding: 0 25px;
}

#about {
  padding: 50px 200px 0px 200px;
  text-align: center;
  display: inline;
}

#about img {
  object-fit: cover;
  object-position: 100% 100%; /* positioned top left of the content box */
  width: 250px;
  height: 250px;
  border-radius: 50%;
  filter: drop-shadow(0 0 15px #000);
}

@media screen and (max-width: 900px) {
  #about img {
    width: 230px;
    height: 230px;
    transition: 0.3s all;
  }
}

#about p {
  font-size: 0.7em;
  line-height: 1.4em;
}

.center-me {
  text-align: center;
}

/* the arrow button that takes you to my projects */
div .smooth-scroll-btn {
  font-size: 5rem;
  cursor: pointer;
  color: var(--introtext);
}
div .smooth-scroll-btn:hover {
  color: var(--contact-btn-colour-hover);
}

.arrow-to-projects {
  bottom: 100px;
  width: 100%;
  position: absolute;
  margin-bottom: -10px;
}

.arrow-to-about {
  bottom: -100px;
  width: 100%;
  position: absolute;
}

/* ------------------------------------------<5: #projects SECTION>------------------------------------------ */
#projects {
  padding-top: 200px;
}

.projects-container {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-template-rows: repeat(3, 1fr);
}

/* Media queries for the project images */
@media screen and (min-width: 1910px) { .project img { width: 18.5vw; } }
@media screen and (max-width: 1900px) { .project img { width: 15.9vw; } }
@media screen and (max-width: 1879px) { .project img { width: 18.9vw; } }
@media screen and (max-width: 1595px) { .project img { width: 24.1vw; } }
@media screen and (max-width: 1282px) { .project img { width: 32.1vw; } }
@media screen and (max-width: 969px) { .project img { width: 48.1vw; } }
@media screen and (max-width: 656px) { .project img { width: 97.1vw; } }
