.project {
  background-color: var(--proj-bg) !important;
  border: 1px solid var(--proj-bg) !important;
  box-shadow: 0 1px 3px 0 var(--proj-shadow),0 0 0 1px var(--proj-bg) !important;
  text-align: left;
}

.project, .image {
  border-radius: 10px;
}

.project a {
  text-decoration: none;
}

.project img {
  height: 29vh;
}

.project * {
  color: var(--introtext);
}

.cropped {
  height: 15vh !important;
  object-fit: cover;
}

.image {
  cursor: pointer;
  overflow: hidden;
  border: 1px solid var(--proj-bg);
}

.proj-title {
  font-size: 1.4rem;
}
.proj-date { 
  font-size: 0.8rem;
}
.proj-details { 
  font-size: 0.94rem;
  padding: 3px 5px 10px 5px;
}

/* Rules for when mouse hovers over a project */
.project a:hover {
  color: var(--text);
}
.project img:hover {
  transition: opacity 0.5s;
  opacity: 0.4;
}
.image:hover + .proj-details > .proj-title > a {
  color: var(--text);
}

/* ------------------------------------------<6: #contact SECTION>------------------------------------------ */
.contact {
  display: block;
  margin: 0 auto;
  text-align: center;
}

.contact i {
  font-size: 50px;
  color: var(--contact-btn-colour);
}

.contact a {
  cursor: pointer;
  text-decoration: none;
  padding: 20px;    /* space out the contact icons */
}

.contact .cv .contact-btn {
  background-color: var(--contact-btn-colour);
  border: 1px solid var(--contact-btn-border-colour);
  color: var(--contact-btn-font-colour);
  opacity: 0.7;
  font-size: 20px;
  border-radius: var(--btn-border-radius);
  padding: 20px;
  display: inline-block;
}

.contact i:hover {
  color: var(--contact-btn-colour-hover);
  transition: 0.3s;
}
.contact-btn:hover {
  background-color: var(--contact-btn-colour-hover) !important;
  transition: 0.3s;
  color: var(--bg1);
}


/* ------------------------------------------<7: BLOG PAGE>------------------------------------------ */
.posts-container {
  position: relative;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  font-size: 1.0rem;
  background-color: rgb(255, 255, 255);
  width: 80%;
  margin: 0 auto;
}

@media only screen and (max-width: 1300px) { table { font-size: 0.9rem; transition: 0.3s all; } }
@media only screen and (max-width: 989px)  { table { font-size: 0.8rem; width: 95%; transition: 0.3s all; } }


table tr, table tr a { 
  text-align: left; 
}

table thead { 
  text-align: center;
  background-color: rgb(248, 248, 248);
}

table th, table td {
  padding: 0.3rem 0.5rem;
  border: 1px solid rgb(237, 237, 237);
}

table td:nth-child(1) { width: 5%; }   /* ID column */
table td:nth-child(2) { width: 55% }   /* TITLE column */
table td:nth-child(3) { width: 15%; }  /* DATE column */
table td:nth-child(4) { width: 25%; }  /* TAGS column */

table tr {
  line-height: 1.4;
}

table tr {
  color: var(--text);
}

table tr a {
  opacity: 0.6;  
  text-decoration: none;
}

table tbody tr a:hover {
  opacity: 1.0;  
}

table tbody tr:hover {
  color: #000;
  background-color: rgb(251, 251, 251);
}

table .tag {
  display: inline-block;
}

table .tag span {
  margin-right: 4px;
  padding: 3px 8px;
  border-radius: 12px;
  font-size: 0.7rem;
  font-weight: bold;
  text-transform: uppercase;
  background-color: rgb(236, 236, 236);
  color: rgb(27, 27, 27);
}

table .tag .internet { background-color: rgb(154, 230, 180); color: rgb(34, 84, 61); }
table .tag .astronomy { background-color: rgb(235, 213, 250); color: rgb(83, 34, 116); }
table .tag .mathematics { background-color: rgb(230, 207, 154); color: rgb(79, 84, 34); }
table .tag .python { background-color: rgb(82, 112, 211); color: rgb(235, 235, 235); }
table .tag .life { background-color: rgb(202, 202, 202); color: rgb(31, 31, 31); }


/* ------------------------------------------<8: BLOG POST PAGE>------------------------------------------ */
.blog-post {
  padding: 0 250px; /* padding: 0px 550px; */
  margin: 0px;
  height: 100%;
  min-height: 100vh;
  border-radius: 0px;
  letter-spacing: 0px;
  font-style: normal;
  transition: 0.3s all;
}

.blog-post-author > svg, 
.blog-post-date > svg,
.universityAssignment .author > svg,
.universityAssignment .date > svg {
  width: 13px;
  margin-right: 15px;
}

.blog-post-title, .blog-post-date, .blog-post-author {
  text-align: center;
}

.blog-post-title {
  color: rgb(33, 33, 33);
  font-size: 6rem;
  font-weight: 900;
}

.blog-post-date {
  font-size: 0.9rem;
  padding-bottom: 40px;
  color: rgb(134, 134, 134);
}

.blog-post-author {
  font-size: 0.9rem;
  padding-bottom: 40px;
  color: rgb(134, 134, 134);
}

.blog-post-content p {
  font-size: 18px;
  color: rgb(66, 66, 66);
  margin-left: 5px;
}

.blog-post-content p.note {
  font-size: 0.9rem;
  color: rgb(153, 153, 153);
  font-style: italic;
}

.blog-post-content ul li {
  white-space: pre-wrap;
}

.blog-post-content ol, .blog-post-content ul {
  font-size: 1.05rem;
  padding: 0 30px;
}

.blog-post-content hr {
  border: 1px solid rgb(200, 200, 200);
  border-radius: 10px;
}

.blog-post-content h1, .blog-post-content h2, .blog-post-content h3, .blog-post-content h4, .blog-post-content h5, .blog-post-content h6 { line-height: 1.3em; margin-bottom: 30px; font-weight: 400; }
.blog-post-content p, .blog-post-content ol, .blog-post-content ul { line-height: 1.7em; }

.blog-post delete-button {
  padding: 10px;
  border-radius: var(--navbar-button-border-radius);
  background-color: var(--navbar-bg-color);
  color: var(--navbar-font-color);
  border: 1px solid var(--navbar-button-border-color);
}

.back-to-previous-page svg {
  margin-bottom: -90px;
  margin-left: -51px;
  width: 25px;
  height: 25px;
  padding: 7px;
  border-radius: 6px;
  border: 2px solid transparent;
}
.back-to-previous-page svg:hover {
  background-color: rgb(238, 238, 238);
}
.back-to-previous-page svg:active {
  border: 2px solid rgba(225, 225, 225, 0.623);
}

.red-text { color: red; }
.blue-text { color: blue; }
.green-text { color: rgb(2, 187, 2); }
.pink-text {color: rgb(221, 0, 173); }


/* ------------------------------------------<9: University>------------------------------------------ */
.wrapper.university {
  /* padding: 100px 125px 100px 50px; */
  padding: 1px 125px;
}

.university .controls {
  /*
  display: flex;
  flex-direction: row;
  float: right;
  */
}

.university .controls .btn {
  border-radius: 100px;
  padding: 7px;
  position: relative;
  display: inline-block;
  background: none;
  border: none;
  cursor: pointer;
}
.university .controls .btn:active {
  background-color: rgba(224, 222, 222, 0.609);
  transition: background-color 250ms ease-in-out;
}

.university .controls svg {
  width: 25px !important;
  height: 25px !important;
  fill: rgb(156, 154, 154);
}

.university .card-grid {
  grid-template-columns: repeat(6, 1fr);
  margin: 0 auto;
  display: grid;
  grid-gap: 24px;
}

.university .card-grid > .card {
  box-shadow: 0 1px 4px 0 rgba(12, 12, 13, 0.1);
  background: rgb(255, 255, 255);
  border: 4px solid rgb(251, 251, 251);
  border-radius: 7px;
  display: flex;
  height: 220px;
  flex-direction: column;
  position: relative;
}
.university .card-grid > .card:hover {
  border: 4px solid rgb(222, 222, 226);
  transition: border 200ms ease-in;
}

.university .card-grid > .card > a.card-link {
  height: 100%;
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: #0060df;
}

.university .card-grid > .card > a.card-link > .card-img-wrapper {
  width: 100%;
  position: relative;
}
.university .card-grid > .card > a.card-link > .card-img-wrapper picture {
  opacity: 1;
  height: 0;
  padding-top: 50%;
  display: block;
  position: relative;
}
.university .card-grid > .card > a.card-link > .card-img-wrapper picture img {
  border-radius: 4px 4px 0 0;
  box-shadow: inset 0 0 0 0.5px rgba(0, 0, 0, 0.15);
  background-color: #d7d7d8;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  max-width: 620px;
}

.university .card-grid > .card > a.card-link > div.card-meta {
  display: flex;
  flex-direction: column;
  padding: 3px 10px;
  flex-grow: 1;
}

div.card-meta > * {
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
  word-break: break-word;
}
div.card-meta p {
  line-height: 20px;
  margin: 0;
}

p.card-module {
  color: rgb(115, 115, 115);
  font-size: 12px;
  margin-bottom: 2px;
}

p.card-assignment {
  color: rgb(12, 12, 13);
  font-size: 16px;
  font-weight: 600;
}

p.card-date {
  color: rgb(126, 126, 126);
  font-size: 11px;
  position: absolute;
  bottom: 0px;
  left: 9px;
}

p.card-year {
  color: rgb(126, 126, 126);
  font-size: 11px;
  position: absolute;
  bottom: 0px;
  right: 9px;
}

div.card-percent {
  color: rgb(255, 255, 255);
  font-size: 11px;
  line-height: 20px;
  margin: 0;
  background-color: rgb(62, 156, 7);
  border-radius: 10px;
  width: 24px;
  position: absolute;
  top: 8px;
  right: 8px;
  text-align: center;
}
