@media only screen and (max-width: 500px) { .contact i { font-size: 35px; transition: 0.3s; } }

/* change font-size of #about section when page is resized */
@media only screen and (max-width: 1150px) { #about p { font-size: 0.7em; transition: 0.3s; } }
@media only screen and (max-width: 920px) { #about p { font-size: 0.65em; transition: 0.3s; } }
@media only screen and (max-width: 730px) { #about p { font-size: 0.6em; transition: 0.3s; } }

@media only screen and (max-width: 640px) { 
  .contact {
      display: grid;
      /* height: 100%;*/
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-template-rows: 1fr 1fr;
      grid-gap: 1px 1px;
      grid-template-areas: "codepen github linkedin email" "cv cv cv cv";
    }
    .contact a {
        padding: 0px;
    }
    .codepen { grid-area: codepen; }
    .github { grid-area: github; }
    .cv { grid-area: cv; grid-column: 1 / 5; }
}

@media only screen and (max-width: 670px) {
  #about {
      margin: 0 auto;
  }
}


/* 
 The custom scrollbar I am using is not available in Firefox, Internet Explorer or Edge, and for those browsers the default scrollbar is used 
 Otherwise, this custom scrollbar will be used.
 Basically if you are using Chrome you get a funky custom scrollbar

Inspiration for scrollbar: https://stackoverflow.com/questions/9251354/css-customized-scroll-bar-in-div
*/
body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  border-radius: 10px;
  background-color: #F5F5F5;
}

body::-webkit-scrollbar {
  width: 12px;
  background-color: rgba(27, 28, 29, 0.87);
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #555;
}